import React from 'react'
// import $ from 'jquery'
import { Link, Switch, Route } from 'react-router-dom'
import "./admin-frame.css"
// import ADMAnalysis from "./admin-sys-analysis/admin-sys-analysis.js"
// import ADMAccountMan from './admin-account-man/admin-acc-mana'
import ADMNTF from './admin-ntf-system/admin-ntf'
import logo from './logo.png'
import 'bootstrap/dist/js/bootstrap.min.js'
import axios from 'axios'
import ADMAna from './admin-sys-analysis/admin-sys-analysis'

export default class AdminFrame extends React.Component {
    constructor(props) {
        super(props)
        this.defaultDirectClass = "dropdown-item"
        this.defaultDirectSize = 3
        this.preLinkDirect = null
        this.AnalysisRef = React.createRef()
    }
    isAdmLogin() {
        axios.post("/auth/admin-is-loggin").then(res => {
            if (!res.data) {
                window.location.replace('/')
            }
            else{
                if (window.location.pathname === '/admin'){
                    console.log("OK")
                    this.AnalysisRef.current.click()
                }
            }
        })
    }

    componentDidMount() {
        this.isAdmLogin()
        console.log(window.location.pathname)
    }

    logoutRequest() {
        axios.post('/logout-request').then(result => {
            if (result) window.location.reload('/')
        })
    }


    render() {
        return (
            <div  style={{ backgroundColor: '#f0f1f7' }}>
                <div>
                    <button id="button-popup" style={{ display: 'none' }} data-toggle="collapse" data-target="#container-popup" aria-controls="container-popup" aria-expanded="true" aria-label="Toggle navigation" />
                    <div className="pop-up collapse" id="container-popup">
                        <div className="background-popup" />
                        <div className="pop-up-main">
                            <div className="container-fluid row" id="myPopup" />
                        </div>
                    </div>
                </div>

                <div className="header">
                    <div className="row">
                        <div className="col-md-2 pt-2 pb-2 bg-dark-l" style={{ borderBottom: "solid 2px #2c3136" }}>
                            <div className="header d-flex justify-content-center">
                                <div className="header-logo mr-3">
                                    <img src={logo} alt="LOGO" style={{ width: '50px', height: '50px' }} />
                                </div>
                                <div className="header-appDes">
                                    <Link className="navbar-brand" to="/admin/analysis">
                                        <span className="span-header">AZbot</span>
                                    </Link>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-10 bg-light d-flex align-items-center">
                            <div className="header-toolbelt d-flex ">
                                <div className="left d-flex">
                                    <Link className="peer-toolbelt" to="/admin/analysis" ref={this.AnalysisRef}>
                                        <i className="fas fa-ellipsis-v" /> Analysis
                                    </Link>
                                    <Link className="peer-toolbelt" to="/admin/message">
                                        <i className="fas fa-bell" /> Message
                                    </Link>
                                </div>
                            </div>
                            <div className="right d-flex ml-auto mr-3">
                                <div className="peer-toolbelt2">
                                    <button onClick={this.logoutRequest} type="button" className="btn btn-outline-primary">
                                        <span><i className="fas fa-sign-out-alt" /></span>
                                    Sign out</button>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

                <div className="body">
                    <Switch>
                        <Route path="/admin/analysis"><ADMAna /></Route>
                        <Route path="/admin/message"><ADMNTF/></Route>
                    </Switch>
                </div>
            </div>

        )
    }
}
