import React from 'react'
import "./account.css"
import axios from 'axios'
import Chart from 'chart.js'
export default class ADMANAAcc extends React.Component {
    constructor(props) {
        super(props)
        this.dashRef = React.createRef()
        this.showRef = React.createRef()
        this.changeDashStyle = this.changeDashStyle.bind(this)
        this.transformGrowth = this.transformGrowth.bind(this)
        this.handleKeyPress = this.handleKeyPress.bind(this)
        this.nowState = [0, 1]
        this.dashGrowthType = ["Weekly", "Monthly"]
        this.showGrowthType = [
            ["Now week", "7 days"],
            ["Now month", "30 days"]
        ]
        this.listRefs = [React.createRef(), React.createRef(), React.createRef(), React.createRef()]
        this.listAccount = []
    }

    async initChart() {
        let returnVal = (await axios.post('/admin-home/get-mini-acc-level')).data
        let tList = [returnVal[0].count, returnVal[1].count, returnVal[2].count]
        new Chart(document.getElementById('canvas-nb1'), {
            type: 'doughnut',
            data: {
                datasets: [{
                    label: "Population (millions)",
                    backgroundColor: ["#b3b3b3", "#425af5", "#b8161e"],
                    data: tList
                }],
                labels: [
                    'Standard',
                    'Intermediate',
                    'Premium'
                ]
            },
            options: {
                title: {
                    display: true,
                    text: 'About customer level',
                    position: "bottom"
                }
                , legend: {
                    position: 'left',
                }
            }
        });
        returnVal = (await axios.post('/admin-home/get-mini-acc-state')).data
        new Chart(document.getElementById('canvas-nb0'), {
            type: 'polarArea',
            data: {
                labels: ["Normal", "Royalty", "Expire"],
                datasets: [
                    {
                        label: "Population (millions)",
                        backgroundColor: ["#3e95cd", "#8e5ea2", "#b8161e"],
                        data: [returnVal[0].count, returnVal[1].count, returnVal[2].count]
                    }
                ]
            },
            options: {
            }
        });
        returnVal = (await axios.post('/admin/get-account-development-dashboard', { dashboardType: 0, displayType: 1 })).data
        this.GrowtChart = new Chart(document.getElementById("canvas-nb2"), {
            type: 'line',
            data: {
                labels: returnVal.labels,
                datasets: [{
                    data: returnVal.data,
                    lineTension: 0,
                    backgroundColor: 'transparent',
                    borderColor: '#007bff',
                    borderWidth: 3,
                    pointBackgroundColor: '#007bff',
                    borderDash: [20, 0.5],
                }
                ]
            },
            options: {
                responsive: true,
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: false,
                            fontSize: 13
                        }
                    }],
                    xAxes: [{
                        ticks: {
                            beginAtZero: false,
                            fontSize: 13
                        }
                    }]
                },
                legend: {
                    display: false
                },
                title: {
                    display: true,
                    fontSize: 16,
                    text: "Growth in the last 7 days"
                },
            }
        });
    }

    componentDidMount() {
        this.initChart()
        this.initTblAccount()
    }

    changeDashStyle(value) {
        this.nowState[value] = 1 ^ this.nowState[value]
        this.dashRef.current.textContent = this.dashGrowthType[this.nowState[0]]
        this.showRef.current.textContent = this.showGrowthType[this.nowState[0]][this.nowState[1]]
        this.transformGrowth()
    }

    async transformGrowth() {
        let returnVal = (await axios.post('/admin/get-account-development-dashboard', { dashboardType: this.nowState[0], displayType: this.nowState[1] })).data
        console.log(returnVal)
        this.GrowtChart.data.datasets[0].data = returnVal.data
        this.GrowtChart.data.labels = returnVal.labels
        this.GrowtChart.update()
    }

    async initTblAccount() {
        this.listAccount = (await axios.post('/adm/get-full-account-to-render')).data
        this.setState({})
    }

    async displayToast(id) {
        document.getElementById('toasts').style.display = 'block'
        let returnVal = (await axios.post('/adm/get-peer-profile',{id:id})).data
        document.getElementById('cusID').textContent = returnVal.id
        document.getElementById('cusLevel').textContent = returnVal.Level
        document.getElementById('cusAddr').textContent = returnVal.Address
        document.getElementById('cusContact').textContent = returnVal.Contact
        document.getElementById('cusEmail').textContent = returnVal.Email
        document.getElementById('cusDOB').textContent = returnVal.DOB
        document.getElementById('cusState').textContent = returnVal.State
        document.getElementById('cusname').textContent = returnVal.FullName
        document.getElementById('countDayLeave').textContent = parseInt(Math.ceil((new Date().getTime() - Date.parse(returnVal.LastAccess))/86400000))
    }
    closeToast() {
        document.getElementById('toasts').style.display = "none"
    }

    
    exportPngChart(index){
        let img = document.getElementById('canvas-nb'+index).toDataURL("image/png")
        let ADownload = document.createElement('a')
        ADownload.href = img;
        ADownload.download = "Img_"+index+".png"
        ADownload.click()
        ADownload.remove()
    }

    handleKeyPress() {
        // Declare variables
        let table, tr, tds, i, j,c;
        table = document.getElementById("tbl-body");
        tr = table.getElementsByTagName("tr");
        let listValue = this.listRefs.map(e => e.current.value.toUpperCase())
        let length = listValue.length
        // Loop through all table rows, and hide those who don't match the search query
        for (i = 0; i < tr.length; i++) {
            tds = tr[i].getElementsByTagName("td");
            j = 0;
            c = true
            for (; j < length; j++) {
                if (listValue[j] === "")
                    continue
                c=false
                if (tds[j].textContent.toUpperCase().indexOf(listValue[j]) > -1) {
                    tr[i].style.display = ''
                }
                else {
                    tr[i].style.display = 'none'
                    break;
                }
            }
            if (c){
                tr[i].style.display=''
            }
        }
    }

    render() {
        return (
            <div style={{paddingLeft:"10px"}}>
                <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                    <h1 className="h2">Dashboard</h1>
                </div>

                <div className="dashboard-frame">
                    <div className="row">
                        <div className="col-md-4">
                            <div className="peer-mini-dashs" style={{ borderRight: "solid 1px black" }}>
                                <div className="mini-dash-body d-flex">
                                    <div className="mini-db-show mr-2">
                                        <canvas id="canvas-nb0" height={"350px"} />
                                    </div>
                                    <p onClick={()=>this.exportPngChart(0)} className="mini-dh-icon ml-auto mr-2"><i className="fas fa-file-export"></i></p>
                                </div>
                            </div>
                            <div className="peer-mini-dashs" style={{ borderRight: "solid 1px black" }}>
                                <div className="mini-dash-body d-flex">
                                    <div className="mini-db-show mr-2">
                                        <canvas id="canvas-nb1" />
                                    </div>
                                    <p  onClick={()=>this.exportPngChart(1)} className="mini-dh-icon ml-auto mr-2"><i className="fas fa-file-export"></i></p>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-8" >
                            <div className="peer-mini-dashs mt-4">
                                <div className="d-flex">
                                    <div className="mini-dh-icon">
                                        <div className="btn-group mr-2">
                                            <button ref={this.dashRef} type="button" onClick={() => this.changeDashStyle(0)} className="btn btn-sm btn-outline-secondary">Week</button>
                                            <button ref={this.showRef} type="button" onClick={() => this.changeDashStyle(1)} className="btn btn-sm btn-outline-secondary">7 days</button>
                                        </div>
                                    </div>
                                    <p onClick={()=>this.exportPngChart(2)} className="mini-dh-icon ml-auto mr-2"><i className="fas fa-file-export"></i></p>
                                </div>
                                <div className="mini-dash-body d-flex">
                                    <div className="mini-db-show mr-2">
                                        <canvas id="canvas-nb2" width="830px" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <br />
                    <div className="dropdown-divider" style={{ border: "solid 1px gray" }}></div>
                    <h2>Search customer</h2>
                    <br />

                    <div className="toasts" id="toasts" role="alert" aria-live="assertive" aria-atomic="true" style={{display:"none"}}>
                        <div className="toasts-header ">
                            <strong className="mr-auto"><span id="spin-wait" >
                                <div className="spinner-grow text-info" role="status">
                                    <span className="sr-only">Loading...</span>
                                </div></span> About:
                                <span id="cusname">Bùi Phi Long</span></strong>
                            <small><span id="countDayLeave">3</span> days ago</small>
                            <button onClick={this.closeToast} type="button" className="ml-2 mb-1 close" data-dismiss="toast" aria-label="Close">
                                <span aria-hidden="true">×</span>
                            </button>
                        </div>
                        <div className="toasts-body">
                            <div className="d-flex">
                                <p className="noneMargin"><b > Customer ID: </b><i><span id="cusID">04912302183912830912</span></i> </p>
                                <p className="noneMargin ml-auto"><b > Level: </b><i><span id="cusLevel">Normal</span></i> </p>
                            </div>
                            <div className="ewew">
                                <p className="noneMargin"><b > Address: </b><span id="cusAddr">126 Thôn 7, Gia Hiệp, Di Linh, Lâm Đồng</span> </p>
                            </div>
                            <div className="ewew">
                                <p className="noneMargin"><b > Contact: </b><span id="cusContact">0969359810</span> </p>
                            </div>
                            <div className="ewew">
                                <p className="noneMargin"><b > Email: </b><span id="cusEmail">long.builong.bui@hcmut.edu.vn</span> </p>
                            </div>
                            <div className="ewew">
                                <p className="noneMargin"><b > Date of births: </b><span id="cusDOB">10/05/1999</span> </p>
                            </div>
                            <div className="ewew">
                                <p className="noneMargin"><b > State: </b><span id="cusState">Expire</span> </p>
                            </div>
                        </div>
                    </div>


                    <div className="tblSearchAccount mb-5">
                        <div className="row">
                            <div className="col-md-3">

                            </div>
                            <div className="col-md-8"><table className="table">
                                <thead className="thead-head thead-dark">
                                    <tr>
                                        <th scope="col" >
                                            <input ref={this.listRefs[0]} className="ipSearch bg-dark" onKeyUp={this.handleKeyPress} type="text" placeholder=" Customner name"></input>
                                        </th>
                                        <th scope="col">
                                            <input ref={this.listRefs[1]} className="ipSearch bg-dark" onKeyUp={this.handleKeyPress} type="text" placeholder="Contact"></input>
                                        </th>
                                        <th scope="col">
                                            <input ref={this.listRefs[2]} className="ipSearch bg-dark" onKeyUp={this.handleKeyPress} type="text" placeholder="Email"></input>
                                        </th>
                                        <th scope="col">
                                            <input ref={this.listRefs[3]} className="ipSearch bg-dark" onKeyUp={this.handleKeyPress} type="text" placeholder="Last Access"></input>
                                        </th>
                                    </tr>
                                </thead>
                            </table>
                                <div className="tbls">
                                    <table className="table">
                                        <thead className="thead-light">
                                            <tr >
                                                <th scope="col" >------------------------------</th>
                                                <th scope="col" >------------------------------</th>
                                                <th scope="col" >------------------------------</th>
                                                <th scope="col" >------------------------------</th>
                                            </tr>
                                        </thead>
                                        <tbody className="thead-tbody" id='tbl-body' >
                                            {
                                                this.listAccount.map((e, i) => {
                                                    return (
                                                        <tr key={i} onClick={() => this.displayToast(e._id)}>
                                                            <td ><b>{e.Fname + " " + e.Lname}</b></td>
                                                            <td>{e.Contact}</td>
                                                            <td>{e.Email}</td>
                                                            <td>{e.LastAccess}</td>
                                                        </tr>)
                                                })
                                            }
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
