import React from 'react'
import { Link } from 'react-router-dom'
import Chart from 'chart.js'
import axios from 'axios'

export default class Home extends React.Component {

    render() {
        return (
            <div className="row">
                <MINIDevice />
                <MINIAccLevel />
                <MINIIncome />
                <MINIACCState />
                <MINIAccGrowth />
            </div>
        )
    }
}

class MINIACCState extends React.Component {

    async initChart() {
        let returnVal = (await axios.post('/admin-home/get-mini-acc-state')).data
        new Chart(document.getElementById('canvas-nb0'), {
            type: 'polarArea',
            data: {
                labels: ["Normal", "Royalty", "Expire"],
                datasets: [
                    {
                        label: "Population (millions)",
                        backgroundColor: ["#3e95cd", "#8e5ea2", "#b8161e"],
                        data: [returnVal[0].count, returnVal[1].count, returnVal[2].count]
                    }
                ]
            },
            options: {
                title: {
                    display: true,
                    text: 'Account State'
                }
            }
        });
    }

    componentDidMount() {
        this.initChart()
    }

    render() {
        return (
            <div className="col-md-4" >
                <div className="peer-mini-dash">
                    <div className="mini-dash-head d-flex mb-3 pb-2">
                        <div className="mini-dh-title ml-2" style={{ fontWeight: 500 }}>AccountState</div>
                        <Link to="/admin/analysis/account-options" className="mini-dh-icon ml-auto mr-2"><i className="fas fa-ellipsis-h" /></Link>
                    </div>
                    <div className="mini-dash-body d-flex">
                        <div className="mini-db-show mr-2">
                            <canvas id="canvas-nb0" height={"350px"} />
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}

class MINIAccLevel extends React.Component {

    constructor(props) {
        super(props)
        this.initRef = React.createRef()
    }

    async initChart() {
        let returnVal = (await axios.post('/admin-home/get-mini-acc-level')).data
        let tList = [returnVal[0].count, returnVal[1].count, returnVal[2].count]
        try {
            this.initRef.current.textContent = tList.reduce((a, b) => a + b)
        } catch {
        }
        new Chart(document.getElementById('canvas-nb1'), {
            type: 'doughnut',
            data: {
                datasets: [{
                    label: "Population (millions)",
                    backgroundColor: ["#b3b3b3", "#425af5", "#b8161e"],
                    data: tList
                }],
                labels: [
                    'Standard',
                    'Intermediate',
                    'Premium'
                ]
            },
            options: {
                title: {
                    display: true,
                    text: 'About customer level',
                    position: "bottom"
                }
                , legend: {
                    position: 'left',
                }
            }
        });
    }

    componentDidMount() {
        this.initChart()
    }

    render() {
        return (
            <div className="col-md-4">
                <div className="peer-mini-dash">
                    <div className="mini-dash-head d-flex mb-3 pb-2">
                        <div className="mini-dh-title ml-2" style={{ fontWeight: 500 }}>Account Level</div>
                        <Link to="/admin/analysis/account-options" className="mini-dh-icon ml-auto mr-2"><i className="fas fa-ellipsis-h" /></Link>
                    </div>
                    <div className="mini-dash-body d-flex">
                        <div ref={this.initRef} className="mini-db-des d-flex align-items-center justify-content-center" style={{ width: '200px', fontSize: '2em', fontWeight: 500 }}>64</div>
                        <div className="mini-db-show mr-2">
                            <canvas id="canvas-nb1" />
                        </div>
                    </div>
                </div>
            </div>)
    }
}

class MINIIncome extends React.Component {

    componentDidMount() {
        new Chart(document.getElementById("canvas-nb2"), {
            type: 'line',
            data: {
                labels: [15, 16, 17, 17, 18, 18, 19, 19, 19, 20],
                datasets: [{
                    data: [86, 114, 106, 106, 107, 111, 133, 221, 783, 2478]
                }
                ]
            },
            options: {
                title: {
                    display: true,
                    text: 'Total income'
                },
                legend: {
                    display: false
                }
            }
        });
    }

    render() {
        return (
            <div className="col-md-4">
                <div className="peer-mini-dash">
                    <div className="mini-dash-head d-flex mb-3 pb-2">
                        <div className="mini-dh-title ml-2" style={{ fontWeight: 500 }}>Income</div>
                        <div className="mini-dh-icon ml-auto mr-2"><i className="fas fa-ellipsis-h" /></div>
                    </div>
                    <div className="mini-dash-body d-flex">
                        <div className="mini-db-des d-flex align-items-center justify-content-center" style={{ fontSize: '2em', fontWeight: 500 }}>99M</div>
                        <div className="mini-db-show ml-auto">
                            <canvas id="canvas-nb2" />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

class MINIDevice extends React.Component {
    constructor(props) {
        super(props)
        this.initRef = React.createRef()
    }

    async initChart() {
        let returnVal = (await axios.post('/admin-home/get-mini-device')).data
        try {
            this.initRef.current.textContent = returnVal.reduce((a, b) => a + b)
        } catch {
        }
        new Chart(document.getElementById('canvas-nb3'), {
            type: 'doughnut',
            data: {
                datasets: [{
                    label: "Population (millions)",
                    backgroundColor: ["#edeef0", "#0d0d0d"],
                    data: returnVal
                }],
                labels: [
                    'LED',
                    'GPS',
                ]
            },
            options: {
                title: {
                    display: true,
                    text: 'About ratio of device',
                    position: "bottom"
                }
                , legend: {
                    position: 'left',
                }
            }
        });
    }

    componentDidMount() {
        this.initChart()
    }
    render() {
        return (<div className="col-md-4">
            <div className="peer-mini-dash">
                <div className="mini-dash-head d-flex mb-3 pb-2">
                    <div className="mini-dh-title ml-2" style={{ fontWeight: 500 }}>Device</div>
                    <div className="mini-dh-icon ml-auto mr-2"><i className="fas fa-ellipsis-h" /></div>
                </div>
                <div className="mini-dash-body d-flex">
                    <div ref={this.initRef} className="mini-db-des d-flex align-items-center justify-content-center" style={{ width: '200px', fontSize: '2em', fontWeight: 500 }}>12</div>
                    <div className="mini-db-show mr-2">
                        <canvas id="canvas-nb3" />
                    </div>
                </div>
            </div>
        </div>)
    }
}

class MINIAccGrowth extends React.Component {
    constructor(props) {
        super(props)
        this.initRef = React.createRef()
    }

    async initChart() {
        let returnVal = (await axios.post('/admin/get-account-development-dashboard', { dashboardType: 0, displayType: 1 })).data
        this.initRef.current.textContent = returnVal.data.reduce((a, b) => a + b)
        new Chart(document.getElementById("canvas-nb4"), {
            type: 'line',
            data: {
                labels: returnVal.labels,
                datasets: [{
                    data: returnVal.data,
                    lineTension: 0,
                    backgroundColor: 'transparent',
                    borderColor: '#007bff',
                    borderWidth: 3,
                    pointBackgroundColor: '#007bff',
                    borderDash: [20, 0.5],
                }
                ]
            },
            options: {
                responsive: true,
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: false,
                            fontSize: 13
                        }
                    }],
                    xAxes: [{
                        ticks: {
                            beginAtZero: false,
                            fontSize: 13
                        }
                    }]
                },
                legend: {
                    display: false
                },
                title: {
                    display: true,
                    fontSize: 16,
                    text: "Growth in the last 7 days"
                },
            }
        });
    }

    componentDidMount() {
        this.initChart()
    }


    render() {
        return (<div className="col-md-8">
            <div className="peer-mini-dash">
                <div className="mini-dash-head d-flex mb-3 pb-2">
                    <div className="mini-dh-title ml-2" style={{ fontWeight: 500 }}>Account growth</div>
                    <Link to="/admin/analysis/account-options" className="mini-dh-icon ml-auto mr-2"><i className="fas fa-ellipsis-h" /></Link>
                </div>
                <div className="mini-dash-body d-flex">
                    <div ref={this.initRef} className="mini-db-des d-flex align-items-center justify-content-center" style={{ width: '200px', fontSize: '2em', fontWeight: 500 }}>200</div>
                    <div className="mini-db-show mr-2">
                        <canvas id="canvas-nb4" width="700px" />
                    </div>
                </div>
            </div>
        </div>)
    }
}