import React from 'react'
import { Link } from 'react-router-dom'
import './ntfstyle.css'
import axios from 'axios'
export default class ADMNTF extends React.Component {
    constructor(props) {
        super(props)
        this.listMessage = []
        this.initListMNMessage = this.initListMNMessage.bind(this)
        this.handleMessage = this.handleMessage.bind(this)
        this.responseMailToast = this.responseMailToast.bind(this)
        this.refName = React.createRef()
        this.refEmail = React.createRef()
        this.refMessage = React.createRef()
        this.refWonder = React.createRef()
        this.refBtnReply = React.createRef()
        this.nowResponse = ''
        this.nowEmail = ''
    }


    async initListMNMessage() {
        this.listMessage = (await axios.post('/adm-message/get-mini-list')).data
        this.setState({})
    }
    componentDidMount() {
        this.initListMNMessage()
    }

    async handleMessage(data) {
        this.nowResponse = data._id
        this.refName.current.innerText = data.Fname + " " + data.Lname;
        this.nowEmail = this.refEmail.current.innerText = data.Email
        document.getElementById('span-time').innerText = parseInt((new Date() - new Date(data.Date)) / (3600000))
        this.refMessage.current.innerText = (await axios.post('/adm-message/get-full-msg', { userID: data._id })).data
        this.refWonder.current.style.display = "none"
        this.refBtnReply.current.style.display = "inline-block"
    }
    
    initOriginalValue(){
        this.refName.current.innerText = "Customer name";
        this.nowEmail = "Customer email"
        document.getElementById('span-time').innerText = 0
        this.refMessage.current.innerText = ""
        this.refWonder.current.style.display = "inline-block"
        this.refBtnReply.current.style.display = "none"
    }

    showToast() {
        document.getElementById('toastss').classList.toggle('show')
    }

    responseMailToast() {
        let text = document.getElementById('toastTextArea').value
        axios.post('/adm-message/send-mail', { message: text, userID:this.nowResponse, recieveMail:this.nowEmail })
        let index = this.listMessage.findIndex(e => e._id === this.nowResponse)
        this.listMessage.splice(index, 1)
        this.setState({ })
        this.initOriginalValue()
        this.showToast()
    }

    render() {
        return (
            <div className="row">
                <div className="toastss" id="toastss" role="alert" aria-live="assertive" aria-atomic="true" >
                    <div className="toasts-header ">
                        <button onClick={this.showToast} type="button" className=" mb-1 mr-2 close" data-dismiss="toast" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                        <strong><span id="res-email">Bùi Phi Long</span></strong>
                        <button onClick={this.responseMailToast} type="button" className=" ml-auto mb-1 close" data-dismiss="toast" aria-label="Close">
                            <span aria-hidden="true"><i className="fas fa-paper-plane"></i></span>
                        </button>
                    </div>
                    <div className="toasts-body">
                        <p className="noneMargin"><b > Email: </b><span id="cusEmail">long.builong.bui@hcmut.edu.vn</span> </p>
                        <div className="input-group">
                            <textarea style={{ height: "300px" }} id="toastTextArea" className="form-control" aria-label="With textarea" defaultValue={""} />
                        </div>

                    </div>
                </div>
                <div className="col-md-2 bg-dark-linear"  >
                    <Toolbalt />
                </div>
                <div className="col-md-10 maining" >
                    <div className="row">
                        <div className="col-md-4 mini-message" >
                            {
                                this.listMessage.map((e, i) =>
                                    <PMMessage key={i} data={e} handleMessage={this.handleMessage} />
                                )
                            }
                        </div>
                        <div className="col-md-7 response-message">
                            <div className="jumbotrons">
                                <div className="d-flex justify-content-between align-items-center">

                                    <div className="ml-auto">
                                        <button ref={this.refBtnReply} onClick={this.showToast} className="btn btn-info btn-lg" style={{ display: "none" }} >Reply</button>
                                    </div>
                                </div>
                                <h1 className="display-4" ref={this.refName}>Customer name</h1>
                                <p className="lead" ref={this.refEmail}>Customer email</p>
                                <small><i> <span id={"span-time"} >0</span> hours ago </i></small>
                                <hr className="my-4" />
                                <div className="">
                                    <div className="" ref={this.refWonder} style={{ width: "100%", }}>
                                        <div className="d-flex justify-content-center">
                                            <div className="spinner-grow text-info" role="status">
                                                <span className="sr-only">Loading...</span>
                                            </div>
                                        </div>
                                    </div>
                                    <p ref={this.refMessage} >

                                    </p>

                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

class PMMessage extends React.Component {
    constructor(props) {
        super(props)
        this.refName = React.createRef()
    }
    render() {
        return (
            <div className="peer-mini-message" onClick={() => this.props.handleMessage(this.props.data)}>
                <div className="peer-mini-heading d-flex justify-content-between">
                    <div className="peer-mini-cusname">
                        {this.props.data.Fname + " " + this.props.data.Lname}
                    </div>
                    <div className="peer-mini-cusmail">
                        {this.props.data.Email}
                    </div>
                </div>
                <div className="dropdown-divider" style={{ border: "solid 1px lightgray" }}></div>
                <div className="peer-mini-mesdescrp">
                    {this.props.data.message}
                </div>
            </div>)
    }
}

class Toolbalt extends React.Component {
    render() {
        return (
            <div className=" pt-2 pb-2 styling">
                <div className="listActive text-light pl-4" style={{ fontWeight: 500 }}>
                    <div className="active-header">
                        <p className="head-title" id="authSignUp" type="button" data-toggle="collapse" data-target="#dashboardNav" aria-controls="dashboardNav" aria-expanded="true" aria-label="Toggle navigation">
                            <i className="fas fa-fill-drip" /> Patrons
</p>
                        <p className="dropdown-divider" />
                        <div className="activeChildrenList collapse" id="dashboardNav">
                            <p className="per-child">Standard </p>
                            <p className="per-child">Advance </p>
                        </div>
                    </div>
                    <div className="active-header">
                        <p className="head-title" id="authSignUp" type="button" data-toggle="collapse" data-target="#dashboardNav2" aria-controls="dashboardNav2" aria-expanded="true" aria-label="Toggle navigation">
                            <i className="fas fa-chart-line" /> Guest
</p>
                        <p className="dropdown-divider" />
                        <div className="activeChildrenList collapse" id="dashboardNav2">
                            <Link className="per-child" to="/admin/analysis/account-options">Account </Link>
                            <Link className="per-child" to="/admin/analysis/device-options">Device </Link>
                            <Link className="per-child" to="/admin/analysis/income-information">Income </Link>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}